import tabHead from "../../components/tabhead.vue";
import lazyImg from '@/components/lazyImg/lazyImg.vue'
export default {
	data() {
		return {
			isChange: -1,
			isLevel: -1,
			isStar: -1,
			sex: ['男', '女'],
			level: ['特级', '高级', '中级', '初级'],
			star: ['五星', '四星', '三星', '二星', '一星'],
			obj: {
				sex: '',
				level: '',
				star: ''
			},
			list: ['https://tool.changbaishan.gov.cn/tongcheng//upload/26c7bd95-dab1-4131-871d-99a68c24b710.jpg']
		};
	},
	components: {
		tabHead,
		lazyImg
	},
	methods: {
		// 性别选择
		clickBtn(index) {
			if (index != this.isChange) {
				this.isChange = index;
				this.obj.sex = this.sex[index]
			} else {
				this.isChange = -1;
				this.obj.sex = ""
			}
		},
		// 级别选择
		levelBtn(index) {
			if (index != this.isLevel) {
				this.isLevel = index;
				this.obj.level = this.level[index]
			} else {
				this.isLevel = -1;
				this.obj.level = ""
			}
		},
		// 星级选择
		starBtn(index) {
			if (index != this.isStar) {
				this.isStar = index;
				this.obj.star = this.star[index]
			} else {
				this.isStar = -1;
				this.obj.star = ""
			}
		},
		searchResult() {
			console.log(this.obj)
			uni.navigateTo({
				url: "../guideList/guideList"
			})
		}
	}
}
